@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Raleway:ital,wght@0,100..900;1,100..900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Salsa&display=swap');

* {
  font-family: "Raleway", sans-serif;
}
body::-webkit-scrollbar {
  width: 5px;
}
body::-webkit-scrollbar-thumb {
  background: #5ae4a7;
}
nav {
  background: rgba(255, 255, 255,.9);
}
nav .tabs li{
  box-shadow: 0 0 10px rgba(0,0,0,.3);
  cursor: pointer;
  transition: .4s;
}
.headings:first-child h1:first-child {
  font-weight: 800;
  background: linear-gradient(#054327, #5ae4a7);
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;

}
.btn button {
  position: relative;
  transition: .4s;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;

}
.btn button::before {
  content: '';
  transition: .4s;
  position: absolute;
  z-index: -1;
  border-radius: 100%;
  background: #2563EB;
  height: 100%;
  width: 100%;
  margin-top: 70%;
}
.btn button:hover::before {
  margin-top: 0;
  border-radius: 0;
  color: #fff;
}
.btn button:hover {
  box-shadow: 0 0 10px rgba(0,0,0,.5);
  transform: scale(1.03);
  color: #fff;
}

.scrolling-text {
  width: 100%;
  overflow: hidden;
  padding: 0 20px;
}
.scrolling-text h1 {
  text-shadow: -5px 5px 5px rgba(0,0,0,.5);
  text-wrap: nowrap;
  animation: scroll 6s linear infinite;
}
@keyframes scroll {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-100%);
  }
}
.course h1:nth-child(1) {
  border-bottom: 2px solid rgba(0,0,0,.3);
}
.home .left {
  margin-top: 50px;
}

@media (max-width:1050px) {
  nav .menu {
    position: fixed;
    top: 0;
    right: 0;
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    background: rgba(255, 255, 255,.9);
    padding: 30px;
    transition: .4s;
  }
  nav .menu.openMenu {
    width: 0;
    right: -40%;
  }
  nav ul li {
    font-size: 40px;
    background: white;
  }
  .open, .close {
    display: block;
  }
  .home,.about {
    height: 90vh;
    flex-direction: column;
  }
}

@media (max-width:400px) {
  nav {
    padding: 10px 0;
  }
  nav .logo img {
    width: 160px;
    padding: 10px;
  }
  nav .tabs li{
    color: #111;
    border-radius: 0px;
    cursor: pointer;
    transition: .4s;
    background: none;
    box-shadow: none;
    border-bottom: 2px solid #9f9f9f;
  }
  nav .tabs a:hover {
    background: #bcbcbc;
  }
  nav .tabs a:nth-child(5) li {
    color: #fff;
    background: rgb(0, 165, 0);
    border-radius: 20px;
  }
  .open {
    font-size: 30px;
  }
  .content {
    margin-top: 80px;
    margin-left: 10px;
    display: flex;
    flex-direction: column;
  }
  .btn {
    margin-top: 10px;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .home .left {
    padding: 0;
    margin-top: 20px;
  }
  .home .right {
    padding: 0;
    margin-bottom: 20px;
  }
  .home .right img {
    width: 300px;
  }
  .headings {
    width: fit-content;
  }
  .headings:nth-child(2) {
    padding-left: 20px;
    gap: 10px;
  }
  .headings:first-child h1:first-child {
    font-size: 13vw;
  }
  .headings:first-child h1:nth-child(2) {
    font-size: 7vw;
  }
  .headings:nth-child(2) h1:nth-child(1) {
    font-size: 7vw;
  }
  .headings:nth-child(2) h1:nth-child(2) {
    font-size: 16vw;
  }
  .headings:nth-child(3) h1 {
    font-size: 5vw;
  }
  .footerLinks {
    padding: 30px;
    flex-direction: column;
    gap: 50px;
  }
  .aboutp p{
    font-size: 20px;
  }
  .aboutimage {
    order: 2;
  }
  .aboutcontent p {
    width: 100%;
    font-size: 15px;
    line-height: 1;
  }
  .aboutimage {
    margin-top: 20px;
  }
  .aboutpage {
    padding: 80px 10px;
    padding-bottom: 10px;
  }
  .scrolling-text {
    height: 70px;
  }
  .scrolling-text h1 {
    font-size: 40px;
  }
}

@media (max-width:380px){
  .home .right {
    padding: 0;
    margin-bottom: 70px;
  }
}